import React, { useState, useEffect, useRef } from "react";
import YouTube, { YouTubePlayer, YouTubeEvent } from "react-youtube";
import {
  Box,
  IconButton,
  Slider,
  Typography,
  Paper,
  useTheme,
  useMediaQuery,
  Button,
} from "@mui/material";
import {
  PlayArrow,
  Pause,
  SkipPrevious,
  SkipNext,
  VolumeUp,
  VolumeOff,
  RepeatOne,
  Repeat,
  RepeatOneOn,
  Shuffle,
  PlaylistAdd,
} from "@mui/icons-material";
import { Video } from "../types";
import "../App.css";

//        needsUserInteraction  
export const NowPlayingContent: React.FC<{
  currentVideo: Video | null;
  isPlaying: boolean;
  currentTime: number;
  duration: number;
  volume: number;
  isMuted: boolean;
  videoViews: string;
  onToggleMute: (e: React.MouseEvent) => void;
  togglePlayPause: (e?: React.MouseEvent) => void;
  handleVolumeChange: (event: Event, newValue: number | number[]) => void;
  handleProgressChange: (event: Event, newValue: number | number[]) => void;
  handlePrevious: (e: React.MouseEvent) => void;
  handleNext: (e: React.MouseEvent) => void;
  formatTime: (time: number) => string;
  hasNextTrack: boolean;
  hasPreviousTrack: boolean;
  needsUserInteraction?: boolean;
  attemptUnmute?: () => void;
  repeatMode?: "none" | "one" | "all";
  onRepeatModeChange?: (mode: "none" | "one" | "all") => void;
  shuffleEnabled?: boolean;
  onShuffleChange?: (enabled: boolean) => void;
  isPlayingPlaylist?: boolean;
  onAddToPlaylist?: (video: Video) => void;
}> = ({
  currentVideo,
  isPlaying,
  currentTime,
  duration,
  volume,
  isMuted,
  videoViews,
  onToggleMute,
  togglePlayPause,
  handleVolumeChange,
  handleProgressChange,
  handlePrevious,
  handleNext,
  formatTime,
  hasNextTrack,
  hasPreviousTrack,
  needsUserInteraction,
  attemptUnmute,
  repeatMode = "none",
  onRepeatModeChange,
  shuffleEnabled = false,
  onShuffleChange,
  isPlayingPlaylist = false,
  onAddToPlaylist,
}) => {
  if (!currentVideo) return null;

  //    
  const handleToggleRepeat = (e: React.MouseEvent) => {
    e.stopPropagation();
    if (onRepeatModeChange && isPlayingPlaylist) {
      if (repeatMode === "none") {
        onRepeatModeChange("all");
      } else if (repeatMode === "all") {
        onRepeatModeChange("one");
      } else {
        onRepeatModeChange("none");
      }
    }
  };

  //    
  const handleToggleShuffle = (e: React.MouseEvent) => {
    e.stopPropagation();
    if (onShuffleChange && isPlayingPlaylist) {
      onShuffleChange(!shuffleEnabled);
    }
  };

  //  
  const handleAddToPlaylist = (e: React.MouseEvent) => {
    e.stopPropagation();
    if (onAddToPlaylist && currentVideo) {
      onAddToPlaylist(currentVideo);
    }
  };

  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        width: "100%",
        p: 2,
        pt: 0,
      }}
    >
      {/* iOS    - iOS   */}
      {needsUserInteraction && (
        <Box
          sx={{
            width: "100%",
            bgcolor: "rgba(0,0,0,0.7)",
            py: 1,
            px: 2,
            borderRadius: 2,
            mb: 2,
            textAlign: "center",
          }}
        >
          <Typography
            variant="body2"
            sx={{ color: "#1db954", fontWeight: "bold" }}
          >
               
          </Typography>
          <Button
            variant="outlined"
            size="small"
            color="primary"
            sx={{ mt: 1, borderColor: "#1db954", color: "#1db954" }}
            onClick={() => attemptUnmute && attemptUnmute()}
          >
             
          </Button>
        </Box>
      )}

      {/*   */}
      <Box
        sx={{
          width: "85%",
          maxWidth: 320,
          aspectRatio: "1",
          borderRadius: 2,
          overflow: "hidden",
          boxShadow: "0 8px 16px rgba(0,0,0,0.3)",
          mb: 3,
          mt: 2,
        }}
      >
        <img
          src={currentVideo.thumbnail}
          alt={currentVideo.title}
          style={{
            width: "100%",
            height: "100%",
            objectFit: "cover",
          }}
          onClick={() => attemptUnmute && attemptUnmute()}
        />
      </Box>

      {/*   */}
      <Box sx={{ width: "100%", mb: 3, textAlign: "center" }}>
        <Typography variant="h6" sx={{ fontWeight: "bold", mb: 0.5 }}>
          {currentVideo.title}
        </Typography>
        <Typography variant="body2" color="text.secondary" sx={{ mb: 0.5 }}>
          {currentVideo.channelTitle}
        </Typography>
        <Typography variant="caption" color="text.secondary">
          {videoViews || " "}
        </Typography>
      </Box>

      {/*  */}
      <Box sx={{ width: "100%", mb: 2 }}>
        <Slider
          value={currentTime}
          max={duration || 100}
          onChange={handleProgressChange}
          sx={{
            color: "#1db954",
            height: 4,
            "& .MuiSlider-thumb": {
              width: 12,
              height: 12,
            },
          }}
        />
        <Box sx={{ display: "flex", justifyContent: "space-between", mt: 0.5 }}>
          <Typography variant="caption" color="text.secondary">
            {formatTime(currentTime)}
          </Typography>
          <Typography variant="caption" color="text.secondary">
            {formatTime(duration)}
          </Typography>
        </Box>
      </Box>

      {/*   */}
      <Box
        sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          width: "100%",
          gap: 2,
          mb: 2,
        }}
      >
        <IconButton
          onClick={(e) => {
            handlePrevious(e);
            attemptUnmute && attemptUnmute();
          }}
          disabled={!hasPreviousTrack}
          sx={{
            color: "white",
            "&.Mui-disabled": { color: "rgba(255,255,255,0.3)" },
          }}
        >
          <SkipPrevious fontSize="large" />
        </IconButton>

        <IconButton
          onClick={(e) => {
            togglePlayPause(e);
            attemptUnmute && attemptUnmute();
          }}
          className="play-button"
          sx={{
            color: "white",
            bgcolor: "#1db954",
            "&:hover": { bgcolor: "#1ed760" },
            width: 64,
            height: 64,
          }}
        >
          {isPlaying ? (
            <Pause sx={{ fontSize: 32 }} />
          ) : (
            <PlayArrow sx={{ fontSize: 32 }} />
          )}
        </IconButton>

        <IconButton
          onClick={(e) => {
            handleNext(e);
            attemptUnmute && attemptUnmute();
          }}
          disabled={!hasNextTrack}
          sx={{
            color: "white",
            "&.Mui-disabled": { color: "rgba(255,255,255,0.3)" },
          }}
        >
          <SkipNext fontSize="large" />
        </IconButton>
      </Box>

      {/*    */}
      <Box
        sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          width: "100%",
          mb: 2,
          gap: 2,
        }}
      >
        {/*   */}
        <IconButton
          onClick={handleToggleShuffle}
          disabled={!isPlayingPlaylist}
          sx={{
            color: shuffleEnabled ? "#1db954" : "rgba(255,255,255,0.7)",
            "&.Mui-disabled": { color: "rgba(255,255,255,0.3)" },
          }}
        >
          <Shuffle />
        </IconButton>

        {/*    */}
        <IconButton
          onClick={handleToggleRepeat}
          disabled={!isPlayingPlaylist}
          sx={{
            color: repeatMode !== "none" ? "#1db954" : "rgba(255,255,255,0.7)",
            "&.Mui-disabled": { color: "rgba(255,255,255,0.3)" },
          }}
        >
          {repeatMode === "one" ? (
            <RepeatOneOn />
          ) : repeatMode === "all" ? (
            <Repeat sx={{ color: "#1db954" }} />
          ) : (
            <Repeat />
          )}
        </IconButton>

        {/*    */}
        <IconButton
          onClick={handleAddToPlaylist}
          sx={{
            color: "rgba(255,255,255,0.7)",
          }}
        >
          <PlaylistAdd />
        </IconButton>

        {/*   */}
        <Box
          sx={{
            display: "flex",
            alignItems: "center",
            width: "60%",
            maxWidth: 200,
          }}
        >
          <IconButton
            onClick={(e) => {
              onToggleMute(e);
              attemptUnmute && attemptUnmute();
            }}
            sx={{ color: "rgba(255,255,255,0.7)", mr: 1 }}
          >
            {isMuted || volume === 0 ? <VolumeOff /> : <VolumeUp />}
          </IconButton>

          <Slider
            value={isMuted ? 0 : volume}
            onChange={handleVolumeChange}
            min={0}
            max={100}
            sx={{
              color: "#1db954",
              "& .MuiSlider-thumb": {
                width: 12,
                height: 12,
              },
            }}
          />
        </Box>
      </Box>
    </Box>
  );
};

interface MusicPlayerProps {
  currentVideo: Video | null;
  queue: Video[];
  onPrevious: () => void;
  onNext: () => void;
  onQueueUpdate: (queue: Video[]) => void;
  onToggleQueue?: () => void;
  playbackHistory?: Video[];
  isPlayingPlaylist?: boolean;
  hasNextTrack?: boolean;
  hasPreviousTrack?: boolean;
  repeatMode?: "none" | "one" | "all";
  onRepeatModeChange?: (mode: "none" | "one" | "all") => void;
  shuffleEnabled?: boolean;
  onShuffleChange?: (enabled: boolean) => void;
  isMobile?: boolean;
  onNavigateToNowPlaying?: () => void;
}

// App.tsx      
export interface MusicPlayerState {
  isPlaying: boolean;
  currentTime: number;
  duration: number;
  volume: number;
  isMuted: boolean;
  videoViews: string;
  toggleMute: (e: React.MouseEvent) => void;
  togglePlayPause: (e?: React.MouseEvent) => void;
  handleVolumeChange: (event: Event, newValue: number | number[]) => void;
  handleProgressChange: (event: Event, newValue: number | number[]) => void;
  formatTime: (time: number) => string;
  needsUserInteraction?: boolean;
  attemptUnmute?: () => boolean;
  isPlayingPlaylist?: boolean;
}

//       
export type PlayerStateListener = (state: MusicPlayerState) => void;

//  state 
let playerState: MusicPlayerState | null = null;
let stateListeners: PlayerStateListener[] = [];

//    
const notifyStateListeners = (state: MusicPlayerState) => {
  playerState = state;
  stateListeners.forEach((listener) => listener(state));
};

// App.tsx    
export const usePlayerState = (listener: PlayerStateListener) => {
  useEffect(() => {
    stateListeners.push(listener);

    //     
    if (playerState) {
      listener(playerState);
    }

    return () => {
      stateListeners = stateListeners.filter((l) => l !== listener);
    };
  }, [listener]);

  return playerState;
};

//    (mm:ss) -     export
export const formatTimeHelper = (time: number): string => {
  if (isNaN(time)) return "0:00";

  const minutes = Math.floor(time / 60);
  const seconds = Math.floor(time % 60);
  return `${minutes}:${seconds < 10 ? "0" : ""}${seconds}`;
};

const MusicPlayer: React.FC<MusicPlayerProps> = ({
  currentVideo,
  queue,
  onPrevious,
  onNext,
  onQueueUpdate,
  isPlayingPlaylist = false,
  hasNextTrack = false,
  hasPreviousTrack = false,
  repeatMode = "none",
  onRepeatModeChange,
  shuffleEnabled = false,
  onShuffleChange,
  isMobile = false,
  onNavigateToNowPlaying,
}) => {
  //  
  const [isPlaying, setIsPlaying] = useState(false);
  const [duration, setDuration] = useState(0);
  const [currentTime, setCurrentTime] = useState(0);
  const [volume, setVolume] = useState(70);
  const [isMuted, setIsMuted] = useState(false);
  const [showVolumeSlider, setShowVolumeSlider] = useState(false);
  const [videoViews, setVideoViews] = useState<string>("");
  const [isIOS, setIsIOS] = useState(false);
  const [needsUserInteraction, setNeedsUserInteraction] = useState(false);
  const interactionRef = useRef(false);

  //  
  const playerRef = useRef<YouTubePlayer | null>(null);
  const progressIntervalRef = useRef<NodeJS.Timeout | null>(null);

  //   
  const theme = useTheme();
  const isMobileDevice =
    useMediaQuery(theme.breakpoints.down("sm")) || isMobile;

  // YouTube  
  const youtubeOpts = {
    height: "0",
    width: "0",
    playerVars: {
      autoplay: 1,
      controls: 0,
      mute: isIOS ? 1 : 0,
      disablekb: 1,
      fs: 0,
      modestbranding: 1,
      rel: 0,
      playsinline: 1,
      origin: window.location.origin,
      enablejsapi: 1,
    },
  };

  //    
  const handleReady = (event: YouTubeEvent) => {
    console.log("YouTube Player Ready, iOS:", isIOS);
    playerRef.current = event.target;

    if (playerRef.current) {
      //   
      try {
        // iOS    
        if (!isIOS) {
          //  iOS 
          playerRef.current.setVolume(volume);
          playerRef.current.unMute();
          setIsMuted(false);
        } else {
          // iOS  -   
          setIsMuted(true);
          console.log(
            "iOS :   ,    "
          );
        }

        //   
        const videoDuration = playerRef.current.getDuration();
        if (videoDuration && videoDuration > 0) {
          setDuration(videoDuration);
        }

        //  
        setIsPlaying(true);
        playerRef.current.playVideo();
        startProgressTracking();

        //   
        setTimeout(() => {
          try {
            const videoData = playerRef.current?.getVideoData();
            if (videoData && videoData.view_count) {
              const views = parseInt(videoData.view_count);
              if (!isNaN(views)) {
                setVideoViews(formatViewCount(views));
              }
            }
          } catch (error) {
            console.log("    ");
          }
        }, 1000);
      } catch (error) {
        console.error("YouTube    :", error);
      }
    }
  };

  //    
  const handleStateChange = (event: YouTubeEvent) => {
    const playerState = event.data;
    console.log("YouTube Player State Changed:", playerState);

    if (playerState === 1) {
      // 
      console.log("YouTube Player: Playing");
      setIsPlaying(true);
      startProgressTracking();
    } else if (playerState === 2) {
      // 
      console.log("YouTube Player: Paused");
      setIsPlaying(false);
      stopProgressTracking();
    } else if (playerState === 0) {
      // 
      console.log("YouTube Player: Ended");
      handleVideoEnd();
    } else if (playerState === 3) {
      // 
      console.log("YouTube Player: Buffering");
      //      
      if (!progressIntervalRef.current) {
        startProgressTracking();
      }
    } else if (playerState === 5) {
      //  
      console.log("YouTube Player: Video cued");
    } else if (playerState === -1) {
      //  
      console.log("YouTube Player: Unstarted");
      //     
      if (playerRef.current) {
        setTimeout(() => {
          try {
            playerRef.current?.playVideo();
          } catch (e) {
            console.error("   :", e);
          }
        }, 1000);
      }
    }

    //   
    if (playerRef.current && playerState !== -1) {
      try {
        const videoDuration = playerRef.current.getDuration();
        if (videoDuration && videoDuration > 0) {
          setDuration(videoDuration);
        }
      } catch (e) {
        console.error("   :", e);
      }
    }
  };

  // / 
  const togglePlayPause = (e?: React.MouseEvent) => {
    if (e) {
      e.stopPropagation();
    }

    if (!playerRef.current) {
      console.log("Player reference not available");
      return;
    }

    console.log("Toggle Play/Pause, current state:", isPlaying);
    try {
      if (isPlaying) {
        playerRef.current.pauseVideo();
      } else {
        playerRef.current.playVideo();
      }
    } catch (error) {
      console.error("/   :", error);
    }
  };

  //    
  const startProgressTracking = () => {
    stopProgressTracking();

    progressIntervalRef.current = setInterval(() => {
      if (playerRef.current && isPlaying) {
        const time = playerRef.current.getCurrentTime();
        setCurrentTime(time);
      }
    }, 500);
  };

  //    
  const stopProgressTracking = () => {
    if (progressIntervalRef.current) {
      clearInterval(progressIntervalRef.current);
      progressIntervalRef.current = null;
    }
  };

  //  
  const handleVolumeChange = (event: Event, newValue: number | number[]) => {
    const value = newValue as number;
    setVolume(value);

    if (playerRef.current) {
      playerRef.current.setVolume(value);
      if (value === 0) {
        setIsMuted(true);
      } else if (isMuted) {
        playerRef.current.unMute();
        setIsMuted(false);
      }
    }
  };

  //  
  const toggleMute = (e: React.MouseEvent) => {
    e.stopPropagation();

    if (!playerRef.current) return;

    if (isMuted) {
      playerRef.current.unMute();
      playerRef.current.setVolume(volume);
      setIsMuted(false);
    } else {
      playerRef.current.mute();
      setIsMuted(true);
    }
  };

  //   
  const handleProgressChange = (event: Event, newValue: number | number[]) => {
    const value = newValue as number;
    if (playerRef.current) {
      playerRef.current.seekTo(value, true);
      setCurrentTime(value);
    }
  };

  //   
  const handleVideoEnd = () => {
    if (repeatMode === "one") {
      //   
      if (playerRef.current) {
        playerRef.current.seekTo(0, true);
        playerRef.current.playVideo();
      }
    } else if (queue.length > 0 || hasNextTrack || repeatMode === "all") {
      //   
      onNext();
    } else {
      //   
      setIsPlaying(false);
      setCurrentTime(0);
    }
  };

  //   (1000 -> 1, 1000000 -> 100)
  const formatViewCount = (count: number): string => {
    if (count >= 1000000) {
      return `${Math.floor(count / 1000000)} `;
    } else if (count >= 1000) {
      return `${Math.floor(count / 1000)} `;
    } else {
      return `${count} `;
    }
  };

  //    (mm:ss)
  const formatTime = (time: number): string => {
    return formatTimeHelper(time);
  };

  //    
  const attemptUnmute = () => {
    if (playerRef.current && needsUserInteraction) {
      try {
        console.log("  ");
        playerRef.current.unMute();
        playerRef.current.setVolume(volume);
        setIsMuted(false);
        setNeedsUserInteraction(false);
        return true;
      } catch (err) {
        console.error("    :", err);
      }
    }
    return false;
  };

  //  
  useEffect(() => {
    return () => stopProgressTracking();
  }, []);

  //     
  useEffect(() => {
    console.log("currentVideo changed:", currentVideo?.title);
    setCurrentTime(0);
    setVideoViews("");

    if (currentVideo) {
      setIsPlaying(true);

      //        
      if (playerRef.current) {
        try {
          //       
          const currentVideoId = playerRef.current.getVideoData()?.video_id;
          if (currentVideoId !== currentVideo.id) {
            console.log("Loading new video:", currentVideo.id);
            playerRef.current.loadVideoById(currentVideo.id);
          } else {
            //    
            console.log("Restarting current video");
            playerRef.current.seekTo(0, true);
            playerRef.current.playVideo();
          }
        } catch (error) {
          console.error("   :", error);
        }
      }
    } else {
      setIsPlaying(false);
      stopProgressTracking();

      //    
      if (playerRef.current) {
        try {
          playerRef.current.stopVideo();
        } catch (error) {
          console.error("   :", error);
        }
      }
    }
  }, [currentVideo]);

  //   
  const toggleVolumeSlider = (e: React.MouseEvent) => {
    e.stopPropagation();
    setShowVolumeSlider(!showVolumeSlider);
  };

  //     (  )
  const handlePrevious = (e: React.MouseEvent) => {
    e.stopPropagation();
    onPrevious();
  };

  //     (  )
  const handleNext = (e: React.MouseEvent) => {
    e.stopPropagation();
    onNext();
  };

  //     
  useEffect(() => {
    const state: MusicPlayerState = {
      isPlaying,
      currentTime,
      duration,
      volume,
      isMuted,
      videoViews,
      toggleMute,
      togglePlayPause,
      handleVolumeChange,
      handleProgressChange,
      formatTime,
      needsUserInteraction,
      attemptUnmute,
      isPlayingPlaylist,
    };

    notifyStateListeners(state);
  }, [
    isPlaying,
    currentTime,
    duration,
    volume,
    isMuted,
    videoViews,
    needsUserInteraction,
    isPlayingPlaylist,
  ]);

  // iOS        
  useEffect(() => {
    // iOS  
    const detectIOS = () => {
      const userAgent = navigator.userAgent;
      const isIOSDevice =
        /iPad|iPhone|iPod/.test(userAgent) ||
        (navigator.platform === "MacIntel" && navigator.maxTouchPoints > 1);

      setIsIOS(isIOSDevice);
      // iOS   
      setNeedsUserInteraction(isIOSDevice);
      console.log("iOS  :", isIOSDevice);
    };

    detectIOS();

    // iOS       
    if (isIOS) {
      const handleUserInteraction = () => {
        if (needsUserInteraction && !interactionRef.current) {
          console.log("  ,   ");
          interactionRef.current = true;

          //    
          if (playerRef.current) {
            try {
              playerRef.current.unMute();
              playerRef.current.setVolume(volume);
              setIsMuted(false);
              setNeedsUserInteraction(false);
            } catch (e) {
              console.error("   :", e);
            }
          }
        }
      };

      //     
      const interactionEvents = ["touchstart", "touchend", "click", "keydown"];
      interactionEvents.forEach((event) => {
        document.addEventListener(event, handleUserInteraction, {
          once: false,
        });
      });

      return () => {
        interactionEvents.forEach((event) => {
          document.removeEventListener(event, handleUserInteraction);
        });
      };
    }
  }, [needsUserInteraction, volume, isIOS]);

  //   
  const renderMiniPlayer = () => {
    if (!currentVideo) return null;

    return (
      <Paper
        elevation={3}
        className="mini-player"
        sx={{
          position: "fixed",
          bottom: isMobileDevice ? 56 : 0,
          left: 0,
          right: 0,
          zIndex: 10,
          borderRadius: 0,
          bgcolor: "background.paper",
          overflow: "hidden",
        }}
        onClick={(e) => {
          if (needsUserInteraction) {
            // iOS     
            if (playerRef.current) {
              try {
                playerRef.current.unMute();
                playerRef.current.setVolume(volume);
                setIsMuted(false);
                setNeedsUserInteraction(false);
              } catch (err) {
                console.error("    :", err);
              }
            }
          }

          //    
          if (onNavigateToNowPlaying) {
            onNavigateToNowPlaying();
          }
        }}
      >
        {/*    */}
        <Box className="progress-container">
          <Box
            className="progress-bar"
            sx={{
              height: 2,
              width: "100%",
              position: "relative",
              bgcolor: "rgba(255,255,255,0.1)",
            }}
          >
            <Box
              className="progress-bar-fill"
              sx={{
                position: "absolute",
                height: "100%",
                left: 0,
                bgcolor: "#1db954",
                width: `${(currentTime / duration) * 100}%`,
              }}
            />
          </Box>
        </Box>

        {/* iOS    - iOS   */}
        {needsUserInteraction && (
          <Box
            sx={{
              width: "100%",
              bgcolor: "rgba(0,0,0,0.7)",
              py: 0.5,
              textAlign: "center",
            }}
          >
            <Typography
              variant="caption"
              sx={{ color: "#1db954", fontWeight: "bold" }}
            >
                
            </Typography>
          </Box>
        )}

        {/*  */}
        <Box
          sx={{
            display: "flex",
            alignItems: "center",
            px: 1.5,
            py: 1,
          }}
        >
          {/*    */}
          <Box
            sx={{
              display: "flex",
              alignItems: "center",
              flex: 1,
              overflow: "hidden",
              mr: 1,
            }}
          >
            <Box
              component="img"
              src={currentVideo.thumbnail}
              alt={currentVideo.title}
              sx={{
                width: isMobileDevice ? 40 : 48,
                height: isMobileDevice ? 40 : 48,
                borderRadius: 1,
                objectFit: "cover",
                mr: 1.5,
              }}
            />
            <Box sx={{ overflow: "hidden" }}>
              <Typography
                variant="body2"
                noWrap
                sx={{ fontWeight: 500, lineHeight: 1.2 }}
              >
                {currentVideo.title}
              </Typography>
              <Typography variant="caption" color="text.secondary" noWrap>
                {currentVideo.channelTitle}
              </Typography>
            </Box>
          </Box>

          {/*   -    */}
          <IconButton
            onClick={(e) => {
              e.stopPropagation();
              if (onShuffleChange && isPlayingPlaylist) {
                onShuffleChange(!shuffleEnabled);
              }
            }}
            disabled={!isPlayingPlaylist}
            size="small"
            sx={{
              color: shuffleEnabled ? "#1db954" : "rgba(255,255,255,0.5)",
              mr: 1,
              "&.Mui-disabled": { color: "rgba(255,255,255,0.3)" },
            }}
          >
            <Shuffle fontSize="small" />
          </IconButton>

          {/*    -    */}
          <IconButton
            onClick={(e) => {
              e.stopPropagation();
              if (onRepeatModeChange && isPlayingPlaylist) {
                if (repeatMode === "none") {
                  onRepeatModeChange("all");
                } else if (repeatMode === "all") {
                  onRepeatModeChange("one");
                } else {
                  onRepeatModeChange("none");
                }
              }
            }}
            disabled={!isPlayingPlaylist}
            size="small"
            sx={{
              color:
                repeatMode !== "none" ? "#1db954" : "rgba(255,255,255,0.5)",
              mr: 1,
              "&.Mui-disabled": { color: "rgba(255,255,255,0.3)" },
            }}
          >
            {repeatMode === "one" ? (
              <RepeatOneOn fontSize="small" />
            ) : repeatMode === "all" ? (
              <Repeat fontSize="small" sx={{ color: "#1db954" }} />
            ) : (
              <Repeat fontSize="small" />
            )}
          </IconButton>

          {/*   */}
          <IconButton
            onClick={(e) => {
              e.stopPropagation();
              togglePlayPause();

              // iOS      
              if (needsUserInteraction && playerRef.current) {
                try {
                  playerRef.current.unMute();
                  playerRef.current.setVolume(volume);
                  setIsMuted(false);
                  setNeedsUserInteraction(false);
                } catch (err) {
                  console.error("    :", err);
                }
              }
            }}
            className="play-button"
            size="small"
            sx={{
              color: "white",
              bgcolor: "#1db954",
              "&:hover": { bgcolor: "#1ed760" },
              width: isMobileDevice ? 36 : 40,
              height: isMobileDevice ? 36 : 40,
            }}
          >
            {isPlaying ? <Pause /> : <PlayArrow />}
          </IconButton>
        </Box>
      </Paper>
    );
  };

  return (
    <>
      {/* YouTube   */}
      <div
        style={{
          position: "fixed",
          left: 0,
          top: 0,
          width: "1px",
          height: "1px",
          opacity: 0.01,
          pointerEvents: "none",
          zIndex: 0,
          overflow: "hidden",
        }}
        className="youtube-player-container"
      >
        {currentVideo && (
          <YouTube
            videoId={currentVideo.id}
            opts={youtubeOpts}
            onReady={handleReady}
            onStateChange={handleStateChange}
            className="youtube-player"
          />
        )}
      </div>

      {/*   */}
      {renderMiniPlayer()}
    </>
  );
};

export default MusicPlayer;
